/* variables */
/* inject:mixins:less */
/* endinject */
/* base */
/*---------- TYPHOGRAFY ----------*/
/* Root elements (use pixels) */
html {
  font-size: 17px !default;
}
/* Standard document elements (use rem) */
.header {
  font-size: 1.5rem;
}
.footer {
  font-size: 0.75rem;
}
.sidebar {
  font-size: 0.85rem;
}
/* Standard module elements (use em) */
h1 {
  font-size: 3em !default;
}
h2 {
  font-size: 2.5em !default;
}
h3 {
  font-size: 2em !default;
}
h4 {
  font-size: 1.5em !default;
}
h5 {
  font-size: 1em !default;
}
/* Links */
a {
  text-decoration: none;
  color: inherit;
}
.container,
.container-fluid {
  position: relative;
  font-size: 1rem;
}
.container {
  width: 90%;
  max-width: 1200px;
}
.container-fluid {
  width: 100%;
}
@media (max-width: 599px) {
  .container {
    font-size: 0.9rem;
    width: 100%;
  }
}
@media (max-width: 400px) {
  .container {
    font-size: 0.8rem;
  }
}
.container-padding {
  padding-left: 10px;
  padding-right: 10px;
}
.section-padding {
  padding: 2em 10px;
}
.section-padding-2x {
  padding: 4em 10px;
}
/* inject:components:less */
/* endinject */
/* inject:partials:less */
.board-links {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.board-cards-container {
  display: flex;
  justify-content: center;
}
.board-cards-container .board-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1500px;
}
.board-cards-container .board-cards .board-card {
  display: flex;
  align-items: center;
  background-image: url("../../../assets/images/ETlogos/logo.svg");
  background-position: left;
  background-repeat: no-repeat;
  background-size: 7em;
  width: 300px;
  margin: 0px;
  padding: 0px;
}
.board-cards-container .board-cards .board-card .board-card-image {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-size: cover;
  margin: 10px 20px;
  border: 1px solid #a03033;
}
.board-cards-container .board-cards .board-card a {
  text-decoration: none;
  color: #a03033;
}
.board-cards-container .board-cards .board-card name {
  font-weight: bold;
}
#board-background {
  background-color: #ef5350;
}
#googlemaps {
  width: 500px;
  height: 400px;
}
.circular {
  border-radius: 50%;
  overflow: hidden;
}
.circular-200 {
  width: 200px;
  height: 200px;
}
.facebook-link {
  color: #133783;
  font-weight: 900;
}
.forgot-container {
  max-width: 30em;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}
.video-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  height: 0;
}
.video-wrapper > iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.programme-header {
  background-color: #fff;
  font-size: 1rem;
}
.programme-header > .header-content {
  margin-top: 20px;
}
.programme-header > .header-content img {
  flex: 0 0 auto;
  margin: 0 2em;
  width: 5em;
}
.programme-header > .header-content .md-display-2 {
  font-size: 3em;
  line-height: 2em;
}
.programme-header > .header-content .md-title {
  font-size: 2.2em;
  margin-left: 2.5em;
}
.skew-lb,
.skew-rb {
  position: relative;
}
.skew-lb > *,
.skew-rb > * {
  z-index: 1;
}
.skew-lb:before,
.skew-rb:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: inherit;
  top: 50%;
}
.skew-lb:before {
  transform: skewY(-3deg);
}
.skew-rb:before {
  transform: skewY(3deg);
}
